import React, { useEffect, useMemo, useRef, useState } from 'react';

import * as s from './style.module.scss';

function Main() {
  const [width, setWidth] = useState<number>(0);

  const ref = useRef<HTMLElement>(null);
  const observer = useRef<ResizeObserver | null>(null);

  useEffect(() => {
    observer.current = new ResizeObserver(([entry]) => {
      setWidth(entry.contentRect.width);
    });

    observer.current.observe(ref.current!);

    return () => {
      observer.current?.disconnect();
    };
  }, []);

  // TODO(dwchung): Add reveal effect.

  return useMemo(
    () => (
      <main className={s.main} ref={ref}>
        <div className={s.wrap}>
          <img
            className={s.icon}
            alt="Wedding Rings"
            src="data:image/png;base64,"
          />
          <p>
            같은 나라와 공간에서 몇번이나 스쳐 지나갔던{' '}
            <span className={s.nowrap}>인연으로 시작해</span>
            {width >= 640 ? <br /> : ' '}
            2020년 비로소 사랑으로{' '}
            <span className={s.nowrap}>맺어진 저희가,</span>
            {width >= 640 ? <br /> : ' '}
            2023년 새해의 길목에서 하나가{' '}
            <span className={s.nowrap}>되려 합니다.</span>
          </p>

          <p>
            이렇게 인생에서 가장 중요하고{' '}
            <span className={s.nowrap}>소중한 날,</span>
            {width >= 640 ? <br /> : ' '}
            양가 부모님 및<span className={s.nowrap}>형제자매 그리고</span>
            {width >= 640 ? <br /> : ' '}
            저희를 오랫동안 지켜보고 아껴주신 감사한{' '}
            <span className={s.nowrap}>분들을 모시고</span>
            {width >= 640 ? <br /> : ' '}
            작은 결혼식을 <span className={s.nowrap}>올리게 되었습니다.</span>
          </p>

          <p>
            부디 자리하시어 저희 긴 여행의 첫걸음을
            {width >= 640 ? <br /> : ' '}
            축복해주시면 감사하겠습니다.
          </p>
        </div>
      </main>
    ),
    [width],
  );
}

export default Main;
